<template>
  <div class="login-container">
    <div class="login-header">
      <div class="login-logo">
        <img src="../../images/login_logo.png" alt="">
        XPE 智能办公平台
      </div>
    </div>
    <!-- 忘记密码 -->
    <div class="login-box">
      <div class="title">
        忘记密码
      </div>
      <div class="login-form margint_50">
        <div class="login-Item">
          <input type="input" placeholder="输入手机号" class="login-input" name="" v-model="phoneNum"  :class="{'login-input-error':phoneNumError}" @blur="checkPhonenum">
          <em v-if="phoneNumError" class="error">{{phoneNumMsg}}</em>
        </div>
        <div class="login-Item">
          <input type="input" placeholder="输入验证码" class="left login-input login-input-code" :class="{'login-input-error':phoneNumValError}" name="" v-model="phoneNumVal"  maxlength="6" @blur="checkPhonenumVal">
          <input class="right login-code-btn"  :class="{'login-code-btn-disabled1':getPhoneCode||phoneNumError||phoneNum==''}" type="button" :value="getPhoneCodeText" @click="getPhoneCodeClick">
          <em class="error" v-if="phoneNumValError">{{phoneNumValMsg}}</em>
        </div>
        <div><input class="login-btn" :class="{'login-btn-disabled':submitOk}" type="button" value="提  交" @click="submitClick">
        </div>
        <router-link to="/login"><a class="login-link">已有账号，去登录>></a></router-link>
      </div>
    </div>
    <!-- <vue-particles
        color="#e1e1e1"
        :particleOpacity="0.5"
        :particlesNumber="15"
        shapeType="circle"
        :particleSize="15"
        linesColor="#cfcfcf"
        :linesWidth="1"
        :lineLinked="true"
        :lineOpacity="0.4"
        :linesDistance="650"
        :moveSpeed="2"
        :hoverEffect="true"
        hoverMode="grab"
        :clickEffect="false"
        clickMode="push"
      >
      </vue-particles> -->
  </div>
</template>

<script>
import {getEcode,apiChangepwdVerify} from '../../config/api'
import{dialogConfirm} from '../../config/mUtils'
//import VueParticles from 'vue-particles'
export default {
  name: 'HelloWorld',
  data () {
    return {
      phoneNum:'',//手机号
      phoneNumMsg:'',
      phoneNumVal:'',//手机验证码
      phoneNumValMsg:'',
      getPhoneCode:false,//是否点击了发送验证码
      getPhoneCodeText:'获取验证码',
    }
  },
  created(){

  },
  components:{
    //VueParticles
  },
  computed: {
    phoneNumError:function(){
      if(this.phoneNum!=''){
        return !/^1\d{10}$/gi.test(this.phoneNum);
      }else{
        return false;
      }
    },
    phoneNumValError:function(){
      if(this.phoneNumVal==''||this.phoneNumVal.length==6){
        return false;
      }else{
        return true;
      }
    },
    submitOk:function(){
      if(this.phoneNumError||this.phoneNum==''||this.phoneNumVal==''||this.phoneNumValError){
        return false;
      }else{
        return true;
      }
    }
  },
  methods:{
    //验证手机号
    checkPhonenum(){
      if(this.phoneNumError){
        this.phoneNumMsg='*手机号格式错误'
      }else{
        this.phoneNumMsg=''
      }
    },
    checkPhonenumVal(){  
      if(this.phoneNumValError){
        this.phoneNumValMsg='验证码格式错误'
      }else{
        this.phoneNumValMsg=''
      }
    },
    //获取短信验证码
    getPhoneCodeClick(){
        if(!this.getPhoneCode&&!this.phoneNumError&&this.phoneNum!=''){
          getEcode({
            phone:this.phoneNum
          }).then(res=>{
            if(res.data.msgNo==1){
              dialogConfirm('success','验证码发送成功')
            }else{
              dialogConfirm('fail',res.data.message)
            }
          });
          var that=this;
          this.getPhoneCode=true;
          var num=60;
          var timer=setInterval(function(){
            num--;
            that.getPhoneCodeText=num+'秒后重新获取';
            if(num===0){
              that.getPhoneCodeText='获取验证码';
              that.getPhoneCode=false;
              clearInterval(timer);
            }
          },1000)
        }else if(this.getPhoneCode){
          dialogConfirm('info','验证发送太频繁啦')
        }
    },
    submitClick(){
      if(this.submitOk){
        apiChangepwdVerify({
          phone:this.phoneNum,
          code:this.phoneNumVal
        }).then(res=>{
          if(res.data.msgNo==1){
            this.$router.push({
              name:'findpwdstep2',
              query:{
                phoneNum:this.phoneNum
              }
            });
          }else{
            dialogConfirm('fail',res.data.message)
          }
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../style/login';
</style>


